<template>
  <div class="consulation">
    <Nav></Nav>
    <!-- <img style="width:100%;" src="../../../static/img/banner6.jpg" alt=""> -->
    <banner :bannerList="bannerList" />
    <div class="content">
      <rightFixed></rightFixed>
      <contentNav></contentNav>
      <div class="contentDiv">
        <p  class="pageTitle" >医院信息平台项目管理服务</p>
        <img :src="content" alt="">
        <p class="contentDes">指导并协助医疗机构设计医院管控模式，梳理和优化核心业务流程，
          制定信息化发展战略和规划(IT规划)、信息化需求分析和设计、信息系统诊断和评价、
          信息系统选型、信息安全管理体系建设、系统实施等业务的咨询服务</p>
      </div>

    </div>
    <publicFoot></publicFoot>
  </div>
</template>

<script>
    import Banner from '@/components/banner';

    export default {
        name: "projectManagement",
        data() {
            return {
                bannerList: [{
                    name: require('../../../static/banner_bg/6.png'),
                    title: '医院信息平台管理服务',
                    type: 'text',
                    text: [
                        '指导并协助医疗机构设计医院管控模式，梳理和优化核心业务流程，制定信息化',
                        '发展战略和规划、信息化需求分析和设计、信息系统诊断和评价、信息系统选型、信息',
                        '安全管理体系建设、系统实施等业务的咨询服务'
                    ]
                }],
                content: require('../../../static/img/other/service.png')
            }
        },
        components: {
            Banner
        }
    }
</script>

<style scoped>
  .contentDiv {
    min-height: 70vh;
  }
  .contentTitle {
    background: white;
    border-bottom: 1px solid gainsboro;
    padding: 2% 4%;
    font-size: 20px;
    font-weight: bold;
    text-align: left;
  }

  .contentDes {
    text-align: left;
    font-size: 14px;
    color: grey;
  }

  .contentDiv img {
    padding: 2% 0;
    width: 90%;
  }
</style>



