<template>
  <div>
		<div style="width:180px;
				    padding:10px;
				    font-size:16px;
				    background: white;
				    float: left;
				    margin-right:1%
			 "
		>
			<div v-for="(item, index) in contentNav"
				 :key="index"
			>
				<el-collapse v-model="activeNames"
							 @change="handleChange"
				>
					<el-collapse-item
								:title="item.name"
								:name="index.toString()"
					>
						<router-link
								class="NavChange"
								v-for="(tem,dex) in item.navList"
								:key="dex"
								:to="{path:tem.url}"
								exact
						>
							{{tem.name}}
						</router-link>
					</el-collapse-item>
				</el-collapse>
			</div>
		</div>
  </div>
</template>

<script>
	export default {
		name: "contentNav",
		data () {
			return {
				activeNames: ['0', '1'],
				contentNav: [
					{
						name: '卫生健康标准咨询与培训',
						navList: [
							{
								name: '互联互通测评咨询',
								url: '/consultation',
							},
							{
								name: '智慧医院服务分级评估',
								url: '/assessment',
							},
							{
								name: '电子病历系统分级评价',
								url: '/Electronic',
							},
							{
								name: 'FHIR标准培训与实施指导',
								url: '/StandardTraining',
							}
						]
					},
					{
						name: '医院信息化建设规划与指导',
						navList: [
							{
								name: '信息化规划与集成方案设计',
								url: '/conceptualDesign',
							},
							{
								name: '医院信息平台项目管理服务',
								url: '/projectManagement',
							},
							// {
							// 	name: 'FHIR标准落地实施指导',
							// 	url: '/guidance',
							// },
							{
								name: '互联网医院筹备与申报/建设与运营',
								url: '/Preparation',
							}
						]
					}
				],
				activeName: '互联互通测评咨询'
			}
		},
		methods: {
			selected(item) {
				this.activeName = item.name;
				this.$router.push({
					path: item.url
				})
			},
			handleChange(val) {
				console.log(val);
			}
		}
	}
</script>
<style>
	.el-collapse-item__content {
		padding-bottom: 25px;
		font-size: 13px;
		color: #303133;
		line-height:20px !important;
	}
</style>

<style scoped>
	.router-link-active {
		text-decoration: none;
		color: red;
		border-left: 3px solid red;
		padding-left: 3%;
	}

	a {
		display: block;
		text-decoration: none;
		color: black;
		font-size: 14px;
	}

	.NavChange {
		/*list-style: none;*/
		margin: 10% 0;
	}
</style>



