<template>
  <div class="consulation">
    <Nav></Nav>
    <!-- <img style="width:100%;" src="../../../static/img/banner5.jpg" alt=""> -->
    <banner :bannerList="bannerList" />
    <div class="content">
      <rightFixed></rightFixed>
      <contentNav></contentNav>
      <div class="contentDiv">
        <p  class="pageTitle" >信息化规划与集成方案设计</p>
        <div class="marginTop">
          <p class="backColorE">信息化规划咨询服务</p>
          <div>
            <p class="ConceTitle">结合国家政策和医疗机构信息化发展的实际情况，为医疗机构提供信息化规划咨询方面的服务，主要内容包括：</p>
            <div class="ConceContent marginTop">
              <div>
                <img src="../../../static/Plan/image1.png" alt="">
                <p>信息化建设项目咨询</p>
                <p>调研并摸底医疗机构信息化程度，明确并细化项目需求、建设原则、目标、内容、实施方案、标准、功能规范、技术规范、项目管理方案及售后服务方案等内容的咨询服务。</p>
              </div>
              <div>
                <img src="../../../static/Plan/image2.png" alt="">
                <p>信息安全咨询</p>
                <p>覆盖组织、管理和技术三个领域，实践并规范信息安全风险评估方法、技术监测监管、应急响应机制，完成信息安全体系建设的咨询服务。</p>
              </div>
              <div>
                <img src="../../../static/Plan/image3.png" alt="">
                <p>信息化管理咨询</p>
                <p>制定信息化发展战略和规划，信息化需求分析和设计、信息系统诊断和评价、信息安全管理体系建设等业务的咨询服务。</p>
              </div>
            </div>
          </div>
        </div>
