<script>
    import Banner from '@/components/banner';

    export default {
        name: "conceptualDesign",
        data() {
            return {
                bannerList: [{
                    name: require('../../../static/banner_bg/5.png'),
                    title: '信息化规划与集成方案设计',
                    type: 'list',
                    text: [
                        '为医疗机构提供信息化规划咨询方面的服务',
                        '指导并协助医疗机构进行全国信息化保障工程的建设'
                    ]
                }],
                content: require('../../../static/img/other/Design.png')
            }
        },
        components: {
            Banner
        }
    }
</script>

<style scoped>

</style>



