            保持与老客户之间的合作关系，长期关注客户发展动态并为客户提供管理改善建议。

          </li>
          <li>
            3.咨询项目实施
            以有效的咨询方法与技术路线，指导咨询项目开展，在项目实施过程中对项目进度、质量、回款进行有效监控。

          </li>
          <li>

            4.团队建设与管理
            对公司和本部门团队人员开展培训培养工作。
          </li>
        </ul>
