          <li>3.有10年以上中型以上企业中层以上管理岗位经历；</li>
          <li> 4.有医疗卫生行业经验；</li>
          <li>5.能独立我你刚才售前沟通、理解客户需求、培训、项目实施等工作；</li>
          <li> 6.语言表达能力、沟通能力、问题分析能力、演讲能力强。</li>
        </ul>
