          咨询顾问</p>
        <p style="font-size: 18px;padding-left: 2%">【岗位职责】</p>
        <ul>
          <li>1.负责与医院领导层进行项目沟通，针对客户需求独立完成项目报告撰写；</li>
          <li> 2.参与项目成果汇报工作，并辅助客户推荐咨询方案落地；</li>
          <li> 3.负责提炼和总结战略/研发/实施其中任一项目的相关知识，并参与公司知识管理工作；</li>
