<template>
  <div>
    <baidu-map class="map" :center="{lng: 104.077612, lat: 30.542438}" :zoom="15">
      <bm-marker :position="{lng: 104.077612, lat: 30.542438}" :dragging="true" animation="BMAP_ANIMATION_BOUNCE">

      </bm-marker>
    </baidu-map>
  </div>
</template>

<script>
  export default {
    name: "test",
    data() {
      return {
        center: {lng: 0, lat: 0},
        zoom: 3
      }
    },
    methods: {
      handler({BMap, map}) {
        console.log(BMap, map)
        this.center.lng = 116.404
        this.center.lat = 39.915
        this.zoom = 15
      }
    }
  }
</script>

<style scoped>
  .map {
    width: 100%;
    height: 400px;
  }
</style>



