<template>
    <div class="banner">
        <el-carousel v-if="bannerList.length > 0">
            <el-carousel-item v-for="(item,index) in bannerList" :key="index">
                <div class="container">
                    <img style="height:100%;width:100%;" :src="item.name" alt="" @click="open" />
                    <div class="info">
                        <h1>{{ item.title }}</h1>
                        <div class="info-box" v-show="item.type == 'text'">
                            <div v-for="(it, i) in item.text" :key="i">{{ it }}</div>
                        </div>
                        <div class="info-box" v-show="item.type == 'list'">
                            <ul>
                                <li v-for="(it, i) in item.text" :key="i">{{ it }}</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </el-carousel-item>
        </el-carousel>

        <!-- <div class="advertisement" v-show="advertisement">
        <img style="height:100%;" src="../../../static/img/Advertisement.png" alt="">
        <div class="close">
            <img src="../../../static/img/close.png" alt="" @click="close">
        </div>
        </div> -->
    </div>
</template>

<script>
    export default {
        name: 'banner',
        props: {
            bannerList: {
                type: Array,
                default: []
            }
        },
        data() {
            return {
                advertisement: false
            }
        },
        mounted() {
            setTimeout(() => {
                this.advertisement = true
            }, 1000)
        },
        methods: {
            close() {
                this.advertisement = false;
            },
            open() {
                // this.$message({
                //   message: '咨询功能暂未开放，敬请期待！',
                //   type: 'warning',
                //   duration: 1000,
                // });
            },
        }
    }
</script>

<style lang="less">
    .el-carousel__container {
        position: relative;
        /*height: 500px !important;*/
    }

    .el-carousel__item[data-v-57b22c0f]:nth-child(2n+1) {
        background-color: rgba(211, 220, 230, 0) !important;
    }

    .el-carousel__button {
        display: block;
        opacity: .48;
        width: 10px !important;
        height: 10px !important;
        border-radius: 100% !important;
        background-color: #FFF;
        border: 1px solid #ea243b;
        outline: 0;
        padding: 0;
        margin: 0;
        cursor: pointer;
        -webkit-transition: .3s;
        transition: .3s;
    }

    .el-carousel__item[data-v-57b22c0f]:nth-child(2n) {
        background-color: rgba(211, 220, 230, 0) !important;
    }

    .banner {
        .el-carousel {
            height: 460px;
            .el-carousel__container {
                height: 100%;
                .el-carousel__item {
                    height: 100%;
                }
            }
        }
    }

    @media screen and (min-width: 1600px) and (max-width: 1680px) {
        .banner {
            .el-carousel {
                height: 400px;
            }
        }
    }

    @media screen and (min-width: 1440px) and (max-width: 1599px) {
        .banner {
            .el-carousel {
                height: 360px;
            }
        }
    }

    @media screen and (min-width: 1366px) and (max-width: 1439px) {
        .banner {
            .el-carousel {
                height: 330px;
            }
        }
    }

    @media screen and (min-width: 1280px) and (max-width: 1365px) {
        .banner {
            .el-carousel {
                height: 300px;
            }
        }
    }

    @media screen and (max-width: 1279px) {
        .banner {
            .el-carousel {
                height: 280px;
            }
        }
    }
</style>

<style scoped lang="less">
    .banner {
        .container {
            position: relative;
            height: 100%;
            .info {
                position: absolute;
                top: 30%;
                left: 15%;
                h1 {
                    margin-bottom: 14px;
                    font-size: 2.5rem;
                    letter-spacing: 2px;
                }
                .info-box {
                    div {
                        margin-bottom: 6px;
                        font-size: 0.9rem;
                    }
                    ul {
                        position: relative;
                        left: 16px;
                        li {
                            margin-bottom: 6px;
                        }
                    }
                }
            }
        }
    }

    .advertisement {
        width: 15.5%;
        height: 400px;
        position: absolute;
        top: 0;
        right: 10%;
        z-index: 55;
        img:nth-child(1) {
            width: 100%;
            height: 100%
        }
    }

    .close {
        position: absolute;
        top: 1%;
        z-index: 55;
        right: 2%;

    }

    .banner {
        position: relative
    }

    .el-carousel__item h3 {
        color: #475669;
        font-size: 18px;
        opacity: 0.75;
        line-height: 500px;
        margin: 0;
    }

    .el-carousel__item:nth-child(2n) {
        background-color: #99a9bf;
    }

    .el-carousel__item:nth-child(2n+1) {
        background-color: #d3dce6;
    }
</style>



