<template>
  <div class="consulation">
    <Nav></Nav>
    <img src="../../../static/img/banner7.jpg" alt="" style="width:100%;">
    <div class="content">
      <rightFixed></rightFixed>
      <contentNav></contentNav>
      <div class="contentDiv">
        <p  class="pageTitle" >FHIR标准落地实施指导</p>
        <p class="contentDes">为医疗机构提供FHIR实施指南及相关的一系列指导服务，帮助医疗机构落地实施。</p>
        <img :src="content" alt="" style="width:100%;">
      </div>
    </div>
    <publicFoot></publicFoot>
  </div>
</template>

<script>
  export default {
    name: "guidance",
    data() {
      return {
        bannerList: [{
            name: require('../../../static/img/other/banner2.png')
        }],
        content: require('../../../static/img/other/ToGround.png')
      }
    }
  }
</script>

<style scoped>
  .contentTitle {
    background: white;
    border-bottom: 1px solid gainsboro;
    padding: 2% 4%;
    font-size: 20px;
    font-weight: bold;
    text-align: left;
  }

  .contentDes {
    text-align: left;
    padding: 2% 0;
    font-size: 14px;
    color: grey;
  }

  .contentDiv {
    min-height: 70vh;
  }

</style>



