                        <li> 2.<span>网络</span>与<span>远程</span>医疗</li>
                        <li> 3.<span>系统对接</span>及安全保护</li>
                    </ul>
                    </li>
                    <li>
                    医疗服务管理体系
                    <ul>
                        <li> 1.<span>规章制度</span>与应急预案</li>
                        <li> 2.<span>不良事件</span>防范与处置流程</li>
                        <li> 3.<span>岗位职责</span>与服务流程</li>
                    </ul>
                    </li>

                </ul>
                </div>
                <div>
                <ul>
                    <li>
                    <span>人员</span>：1.医护人员；2.药师
                    </li>
                    <li>
                    执业规则
                    <ul>
                        <li>1.知情同意</li>
                        <li> 2.开具处方</li>
                        <li>3.<span>电子签名</span></li>
                        <li> 4.<span>在线处方审核</span></li>
                        <li> 5.电子病历</li>
                        <li> 6.患者自助查询</li>
                    </ul>
                    </li>
                    <li>合作协议</li>
                </ul>
