                    </li>
                    <li>参与服务运营临床科室，主任牵头制定医生、护士等服务内容，并推动人员上线和培训；
                        信息部门支持，对接必要的信息化接口；
                    </li>
                    <li>财务对应相关服务的账户对接、收入核算等工作。</li>
