                    <ul>
                    <li>组织产品服务人员，和临床科室沟通服务方案，并负责培训指导科室医务人员具备线上提供服务能力；</li>
                    <li>服务运营人员在院区内、科室内，协助患者了解并购买相关服务，指导帮助患者掌握线上接受服务的能力；</li>
                    <li>对合作开展的服务进行线上宣传，科室/医生品牌宣传工作；</li>
                    <li>对医务人员有偿服务进行履约分配并代缴相关税款。</li>
                    </ul>
