                <div class="preparationLeft">
                    <p>2个月</p>
                    <p>全部科室医生上线</p>
                    <p>75%+</p>
                    <p>各科室上线医生比例不低于此</p>
                </div>
                <div class="preparationRight">
                    <p><span class="titlePone">1 </span><span class="titlePtwo"> 抓牢医生运营。</span>运营专员 提升每个医生活跃度，患者参与度，满意度，帮助医生搭建工作室，提升医生品牌知名度。</p>
                    <p><span class="titlePone">2 </span><span class="titlePtwo"> 拓展医疗体覆盖力。</span>待医院互联网医院运转顺利后，4个月内拓展医联体医院医生上线，争取50个医生开辟专区。充分利用医联体医院覆盖大部分区域患者。
                    </p>
                    <p><span class="titlePone">3 </span><span class="titlePtwo"> 提升活跃。</span>半年内医共体医院医生拓展到150位，并广泛提升活跃度。</p>
                </div>
                </div>
            </div>

            </div>
