<template>
    <div class="NavHead">
        <div class="Nav">
            <div class="navList">
                <!-- LOGO -->
                <div class="navLogo">
                    <img :src="logo" alt="" style="vertical-align: middle">
                </div>
                <!-- Nav -->
                <div class="navMenu" style="width: 60%;">
                    <ul>
                        <li v-for="(item,index) in NavList"
                            :key="index"
                            :class="{active:(item.url == $route.meta.nav)}"
                            @click="changeNav(item.url, index)"
                            @mouseover="move($route.meta.nav)"
                        >
                            {{item.name}}
                        </li>
                    </ul>
                </div>
                <!-- 面板 -->
                <div class="levelTwoNav"
                     v-show="levelTwo"
                     @mouseleave="levelTwo = false"
                >
                    <div v-for="(item,index) in list"
                         :key="index"
                    >
                        <p class="levelTwoNan-header">
                            <span>|</span>
                            <span class="text">{{ item.title }}</span>
                        </p>
                        <ul class="list">
                            <li v-for="(tem,dex) in item.child"
                                :key="dex"
                                @click="changePage(tem.url)"
                                class="item"
                            >
                                {{ tem.name }}
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Nav",
        data() {
            return {
                activeNum: 0,
                activeIndex: '/',
                levelTwo: false,
                logo: require('../../../static/img/index/logo.png'),
                NavList: [
                    {
                        name: '首页',
                        url: '/',
                        id: '1',
                    }, {
                        id: '2',
                        name: '业务领域',
                        url: '/consultation',
                    },
                    {
                        id: '3',
                        name: '合作案例',
                        url: '/CooperationIndex',
                    },
                    // {
                    //     id: '4',
                    //     name: '行业动态',
                    //     url: '/IndustryIndex',
                    // },
                    {
                        id: '5',
                        name: '联系我们',
                        url: '/contactIndex',
                    }, {
                        id: '5',
                        name: '人才招聘',
                        url: '/recruit',
                    }
                ],
                list: [
                    {
                        title: '卫生健康标准咨询与培训',
                        child: [
                            {
                                name: '互联互通测评咨询',
                                url: '/consultation'
                            }, {
                                name: '智慧医院服务分级评估',
                                url: '/assessment'
                            }, {
                                name: '电子病历系统分级评价',
                                url: '/Electronic'
                            }, {
                                name: 'FHIR标准培训与实施指导',
                                url: '/StandardTraining'
                            }
                        ]
                    },
                    {
                        title: '医院信息化建设规划与指导',
                        child: [
                            {
                                name: '信息化规划与集成方案设计',
                                url: '/conceptualDesign'
                            },
                            {
                                name: '医院信息平台项目管理服务',
                                url: '/projectManagement'
                            },
                            // {
                            //     name: 'FHIR标准落地实施指导',
                            //     url: '/guidance'
                            // },
                            {
                                name: '互联网医院筹备与申报',
                                url: '/Preparation'
                            }
                        ]
                    }
                ]
                // activeName: '首页'
            }
        },
        mounted() {
            document.addEventListener('click', e => {
                if (!this.$el.contains(e.target)) {
                this.levelTwo = false //这句话的意思是点击其他区域关闭（也可以根据自己需求写触发事件）
                }
            })
            this.levelTwo = this.$route.path == '/consultation' ? true : false
        },
        //通过路由跳转判断选中的样式
        created() {
            if (this.$route.name == '/') {
                this.activeIndex = '1';
            } else if (this.$route.name == "/consultation") {
                this.activeIndex = '2';
            } else if (this.$route.name == "/CooperationIndex") {
                this.activeIndex = '3';
            } else if (this.$route.name == "/IndustryIndex") {
                this.activeIndex = '4';
            } else if (this.$route.name == "/contactIndex") {
                this.activeIndex = '5';
            }
        },
        methods: {
            mourseMoveEvent (e) {
                console.log(e);
            },
            change(item) {
                if (item.name == '业务领域') {
                    this.levelTwo = this.levelTwo == true ? false : true
                }
            },
            changePage(tem) {
                this.$router.push({
                    path: tem
                })
            },
            changeNav(tem, index) {
                this.$router.push({
                    path: tem
                });
                this.activeNum = index;
            },
            move (tem) {
                if (
                    tem == '/consultation' ||
                    tem == '/assessment' ||
                    tem == '/Electronic' ||
                    tem == '/StandardTraining' ||
                    tem == '/conceptualDesign' ||
                    tem == '/projectManagement' ||
                    tem == '/guidance' ||
                    tem == '/Preparation'
                )
                {
                    this.levelTwo = true
                } else {
                    this.levelTwo = false
                }
            }
        }
  }
</script>

<style scoped lang="less">
    a {
        outline: none;
        text-decoration: none;
    }

    a:hover, a:active {
        -webkit-tap-highlight-color: transparent;
    }

    .navMenu {
        ul {
            li {
                list-style: none;
                float: left;
                padding: 0 2%;
                cursor: pointer;
            }
        }
    }

    .navMenu ul li :hover {
        color: #28d236;
        display: inline-block;
    }

    .active {
        border-top: 2px solid #ea243b;
        color: #ea243b;
    }

    .navList {
        display: flex;
        flex-direction: row
    }

    .navLogo {
        width: 40%;
        text-align: center
    }

    .el-menu--horizontal > .el-menu-item {
        float: left;
        height: 10vh;
        line-height: 10vh;
        margin: 0;
        font-size: 18px;
        border-bottom: 2px solid transparent;
        color: #909399;
    }

    .el-menu--horizontal > .el-menu-item.is-active {
        border-bottom: none !important;
        border-top: 2px solid red;
        color: red;
    }

    .el-menu.el-menu--horizontal {
        border-bottom: none !important;
    }

    .router-link-active {
        text-decoration: none;
        color: red;
        border-top: 3px solid red;

    }

    a {
        text-decoration: none;
        color: black;
        font-size: 20px;
    }

    .NavHead {
        height: 10vh;
        width: 100%
    }

    .Nav {
        border: 1px solid white;
        height: 10vh;
        line-height: 10vh;
        background: white;
        position: fixed;
        z-index: 99;
        top: 0;
        left: 0;
        right: 0;
        font-size: 18px;
    }

    .NavChange {
        padding: 2%;
    }

    .navImg {
        text-align: center;
    }

    .navImg img {
        display: inline-block;
        vertical-align: middle
    }

    .NavHead {
        .Nav {
            .navList {
                .levelTwoNav {
                    display: flex;
                    justify-content: space-between;
                    flex-direction: row;
                    position: absolute;
                    top: 100%;
                    left: 40%;
                    padding: 4px 14px;
                    line-height: 2.2;
                    border-bottom: 2px solid #ea243b;
                    background: rgba(255, 255, 255, 0.98);
                    box-shadow: 0 3px 3px #eee;
                    .levelTwoNan-header {
                        padding-right: 10px;
                        border-bottom: 1px solid #b4b4b4;
                        &:last-child {
                            padding-right: 0;
                        }
                        span {
                            font-size: 14px;
                            &:first-child {
                                color: #ea243b;
                                font-weight: bold;
                            }
                            &:last-child {
                                font-size: 18px
                            }
                            &.text {
                                position: relative;
                                top: 3px;
                            }
                        }
                    }
                    .list {
                        .item {
                            padding: 3px 0;
                            color: grey;
                            font-size: 16px;
                            list-style: inside;
                            cursor: pointer;
                            &:hover {
                                color: #ea243b;
                            }
                        }
                    }
                }
            }
        }
    }

    @media screen and (min-width: 800px) and (max-width: 1440px) {
        .NavHead {
            .Nav {
                .navList {
                    .levelTwoNav {
                        .levelTwoNan-header {
                            span {
                                font-size: 14px;
                                &.text {
                                    position: relative;
                                    top: 2px;
                                }
                            }
                        }
                        .list {
                            .item {
                                font-size: 14px;
                            }
                        }
                    }
                }
            }
        }
    }
</style>



