import Vue from 'vue'
import Nav from './Nav/index.js' // 导航
import publicFoot from './publicFoot/index.js' // 页脚
import banner from './banner/index.js' // 页脚
import contentNav from './contentNav/index.js' // 页脚
import rightFixed from './rightFixed/index.js' // 右边固定

const components = [
    Nav,
    publicFoot,
    banner,
    contentNav,
    rightFixed
];

components.map(component => {
    Vue.component(component.name, component)
});



